/* src/VideoList.css */

.video-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  padding: 20px;
}

.video-item {
  max-width: 560px;
  flex: 1 1 300px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  overflow: hidden;
}

iframe {
  width: 100%;
  height: 315px;
}